body {
  background: #2d2e2e;
  color: #979899; }

main {
  padding: 1rem;
  max-width: 1140px;
  margin: auto; }

.wio-link {
  line-height: 1;
  display: block;
  position: fixed;
  bottom: 1rem;
  right: 1rem; }

.focus {
  background: #fdfdfd;
  width: 1rem;
  height: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  mix-blend-mode: difference; }

*[tabIndex] {
  display: inline-block;
  padding: 1rem; }
  *[tabIndex]:focus {
    outline: 0; }

body {
  margin: 0;
  padding: 0;
}

